html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7eaf6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
  margin: 30px auto 0 auto;
  width: 100%;
  text-align: center;
}

.header-title {
  font-size: 40px;
  font-weight: bold;
  color: #113f67;
  box-sizing: border-box;
}

.header-navigation-dropdown {
  margin: 15px;
}

.header-navigation-dropdown span {
  font-size: 18px;
}

.dropdown {
  display: inline-block;
  width: 200px;
  margin: 0 10px;
  z-index: 999;
}


.subheader-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0;
  width: 100%;
}

.subheader {
  text-align: center;
  color: #113f67;
  border: 3px dashed #38598b;
  font-size: 24px;
  font-weight: 500;
  line-height: 24px;
  border-radius: 5px;
  width: auto;
  padding: 10px 20px;
  margin: 0px 20px;
  z-index: 1;
  max-width: 350px;
}

hr {
  margin-left: 0;
  margin-right: 0;
  width: 30%;
  color: #38598b;
  opacity: 0.7;
}

@media only screen and (max-width: 468px) {
  .subheader {
    font-size: 18px;
  }

  hr {
    display: none;
  }
}

.post-container {
  display: -webkit-flex;
  display: flex;
  /* width: 700px; */
  width: 100%;
  max-width: 700px;
  padding: 20px;
  border-bottom: solid 1px rgba(0,0,0,0.3);
}

.horizontal-image {
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.vertical-image {
  height: 600px;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.vertical-image img,
.horizontal-image img {
  border-radius: 20px;
}

.vertical-image img {
  width: 50%;
  box-sizing: border-box;
}
.horizontal-image img {
  min-height: 400px;
  width: 100%;
  box-sizing: border-box;
}

.description {
  font-size: 15px;
  color: #113f67;
  text-align: justify;
}

.vertical-image .description {
  box-sizing: border-box;
  padding: 0 20px;
}

.horizontal-image .description {
  box-sizing: border-box;
  padding: 20px;
}

.horizontal-image .no-image {
  padding: 0px;
}

@media only screen and (max-width: 710px) {
  .vertical-image, .horizontal-image{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: auto;
    box-sizing: border-box;
  }

  .vertical-image img {
    width: 75%;
  }

  .vertical-image .description {
    padding-top: 20px;
  }
  .horizontal-image img {
    min-height: 0px;
  } 


  /* img, .description {
    max-width: 90vw;
  } */
}
.navigation-controls{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    max-width: 700px;
    margin: 30px auto;
}

.navigation-controls div {
    font-size: 20px;
    color: #113f67;
    cursor: pointer;
}

.navigation-controls div:hover {
    text-decoration: underline;
}
.semester-container,
.post-grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}











.App {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  font-family: "Work Sans", "Roboto", sans-serif;
}

